.nav {
  display: flex;
  justify-content: space-around;
  align-items: center;

  padding: 20px;
  margin-left: 20%;

  border-bottom: 2px solid #f3f3f3;
}

.search-input {
  position: relative;

  width: 14rem;

  padding: 12px 20px;
  margin-right: 20px;

  border: none;
  outline: none;
  border-radius: 5px;

  background: #f6f6f6;
}

.nav-icons {
  width: 1.5rem;
  height: 1.5rem;
  margin-left: 2rem;
  color: #484848;
}
