.btns {
  padding: 10px 20px;
  margin-right: 6px;
  background: transparent;
  border: none;
  border: 0.6px solid #ccc;
  border-radius: 5px;
  color: #323232;
  cursor: pointer;
}

.btns:hover {
  border-color: #525252;
}

.btns-choosen {
  color: #000;
  border-color: #525252;
}